@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios{

    @include m.tablet{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    .servicio{
        border-radius: 1rem;
        text-align: center;
        background-color: v.$blanco;
        margin: 1rem;
        padding: 2rem;
        transition-property: transform;
        transition-duration: 300ms;

        @include m.tablet{
            display: flex;
            flex-direction: column;
            justify-content: center;
        }

        &.seleccionado{
            background-color: v.$azul;
            p{
                color: v.$blanco;
            }
        }

        &:hover{
            transform: scale(1.1);
        }

        p{
            margin: 0 0 calc(v.$separacion /4) 0;
            color: v.$negro;
        }

        .precio-servicio{
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }
    }

}