@use 'base/variables' as v;
@use 'base/mixins' as m;

//EN UI estamos creando el diseño más global, no nos estamos metiendo en ninguna de las secciones. Estamos definiendo la forma general que van a tener nuestro layout.php

.contenedor-app{
    @include m.tablet{ //Si el display es mas grande que una tablet, entonces muestrame el contenido en un grid de dos columnas de 1fr
        display: grid;
        grid-template-columns: 1fr 1fr;
        min-height: 100vh;
    }
    .imagen{
        object-fit: cover;
        width: 100%;
        height: 20rem; //Para que la imagen se vea cuando no este aplicando el m.tablet
        @include m.tablet{ //Para que la altura imagen se adapte a la altura del media query de la linea 7 (Sino se quedaria en 30rem) 
            min-height: 100%; //Al mostrar los servicios se enchancha la imagen si la dejo a auto, por eso le he puesto altura fija
        }
    }
    .app{
        width: 100%; //Para que no llegue hasta las esquinas del contenedor

        padding: 3rem 4rem;
        
        @include m.tablet{ //Para que en la parte de app podamos hacer scroll con el contenido cuando sea mucho (ver globales .body la imagen no es auto con el contenido sino fija a al 100%)
            overflow-y: scroll;
        }
    }

    .barra{
        text-align: center;

        a {
            color: v.$blanco;
            display: block; //Salto de linea por elemento
            margin-bottom: calc(v.$separacion / 2);
            text-align: center;
            margin-bottom: 0;
            font-size: 2rem;

            &:hover{
            color: darken(v.$gris , 30) ;
            }
        }

        @include m.tablet{
            display: flex;
            justify-content: space-between;
            align-items: center;
            
        }

    }
    
    .barra-nav{
        margin-top: 5rem;
        border-radius: 1rem;
        justify-content: flex-start;

        .urlactual{
            box-shadow: 0 3px v.$azul;
        }

        a{


            color: v.$blanco;
            display: block; //Salto de linea por elemento
            margin-right: 2rem;
            margin-bottom: calc(v.$separacion / 2);
            margin-bottom: 1rem;
            font-size: 2.2rem;
            padding: 4px;
            text-align: center;
            transition: box-shadow 0.3s;

            &.actual{
                box-shadow: 0 3px v.$azul;
            }
        }
        
        @include m.tablet{
            display: flex;
            
        }
    }
}
