@use "base/variables" as v;
@use "base/mixins" as m;

.boton {
  @include m.boton(darken(v.$azul, 10%), v.$blanco);
  margin: 0 auto;
  border-radius: 1rem;

  &:hover{
    color: v.$blanco;
  }

}



.margen{
  margin-bottom: 6rem;
}

.boton-eliminar {
  @include m.boton(darken(#B71E01, 10%), v.$blanco);
  animation: fadein 1s;
  
  @include m.tablet{
    width: 100%;
  }
  
  padding: 1rem 4rem;
  margin: 0 auto;
  margin-bottom: 4rem;
  border-radius: 1rem;
}
