@use "base/variables" as v;
@use "base/mixins" as m;


.alerta{
    height: fit-content;
    padding: calc(v.$separacion / 6);
    color: v.$blanco;
    width: 100%;
    display: block;
    margin-bottom: 1rem;
    text-align: center;
    text-transform: uppercase;
    font-weight: v.$regular;
    font-size: 1.8rem;
    border-radius: 1rem;

    animation: fadein 1s;


    &.error{
        background-color: v.$rojo;
    }

    &.exito{
        background-color: v.$verde;
    }
}

.hidden{
    animation: fadeout 1s;
}
.scale{
    animation: escala 2s;
}

.alertas-contenedor{
    padding: 0.5rem;
}
