@use 'base/variables' as v;
@use 'base/mixins' as m;

.card {
    height: 20rem;
    width: 100%;
    margin: 1rem 0;
    animation: fadein 1s;
}

.card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    border-radius: 1rem;
    border: 2px solid darken(v.$gris, 50%);
    background-image: url(../img/2.jpg);
    background-size: cover;;
}


.card-front, .card-back {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    padding: 1rem;
    border-radius: 1rem;
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
    transform: rotateX(0deg); //Soluciona el bug de backfacevisibility en firefox
    
    .acciones{
        margin: 0;
    }
    .formulario{
        margin: 1rem;
    }
    .boton,.boton-eliminar{
        width: 80%;
        display: block;
        margin: 0 auto,;
        padding: 1rem;
        animation: fadein 1s;
        font-size: 1.6rem;
    }
    p{  
        color: v.$azul;
        padding: 0rem 0.5rem;
        margin: 1rem 1rem;
        font-size: 1rem;
        font-weight: 500;

        @include m.telefonosmall{
            font-size: 1.2rem;
        }
        @include m.tablet{
            font-size: 1.6rem;
        }
        @include m.desktop{
            font-size: 2rem;
        }
        span{
            font-weight: 400;
            color: v.$blanco;
        }
    }
}

.flip-card{
    @extend .card;
    height: 25rem;
    &.mis-citas{
        height: 15rem;
    }
}


.flip-card-inner{
    @extend .card-inner;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    margin: 0 auto;
    &.clicked{
        transform: rotateX(180deg);
    }
}

.flip-card-front{
    @extend .card-front;

    grid-template-areas: "fecha hora"
    "cliente  cliente"
    "email  telefono";
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    align-items: center;

    &.card-small{
        grid-template-rows: 1fr 1fr;
    }

    .nota{
        grid-area: cliente;
        font-size: 2rem;
    }

    .hora{
        grid-area: hora;
        text-align: right;
    }
    .fecha{
        grid-area: fecha;
        text-align: left;
    }
    .cliente{
        grid-area: cliente;
        font-size: 2.8rem;
        
    }
    .email{
        grid-area: email;
        text-align: left;
    }
    .telefono{
        grid-area: telefono;
        text-align: right;
    }

}

.flip-card-back {
    @extend .card-back;
    width: 100%;
    height: 100%;
    
    grid-template-areas: 
    "servicio servicio servicio"
    "servicio servicio servicio"
    "preciototal  preciototal preciototal";
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    align-items: center;

    .precio-total{
        font-size: 2rem;
        grid-area: preciototal;
        border: none;
        
        @include m.tablet{
            font-size: 2.8rem;
        }
        
    }

    transform: rotateX(180deg);  
}