@use 'base/variables' as v;
@use 'base/mixins' as m;

//Estilos a la barra de desplazamiento
/* Works on Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: darken(v.$gris, 60%), v.$negro;
  }
  
  /* Works on Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 12px;
  }
  
  *::-webkit-scrollbar-track {
    background: v.$negro;
  }
  
  *::-webkit-scrollbar-thumb {
    background-color: darken(v.$gris, 60%);
    border-radius: 20px;
    border: 3px solid v.$negro;
  }