@use 'base/variables' as v;


.contenedor-servicios, .contenedor-datos{
    p{
        margin-bottom: 1rem;
    }
    h3{
        margin-bottom: 0;
    }
    span{
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }
}

.contenedor-servicio{
    padding: 3rem 0;
    border-bottom: 1px solid v.$gris;

    &:last-of-type{
        margin-bottom: 3rem;
    }
}


