//Podemos usar variables definidas en otros archivos css como por ejemplo:
@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{
    margin-top: 3rem;
    width: 100%;
}

.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;

    label{
        flex: 0 0 10rem; //Crecimiento//Encogecimiento//Base = 100px
    }
    input{
        flex: 1; //Ocupa el resto del flex
        border: none;
        padding: calc(v.$separacion / 4); //50/4
        border-radius: 1rem;

        &:disabled{ //Hay que usar ::placeholder para chrome,se ralla en la visualizacion si usas color:(Este si funciona en firefox dev) 
            background-color: darken(v.$gris, 20%);
            cursor: not-allowed;

            &::placeholder{
                color: darken(v.$gris, 50%);
            }
        }

    }

}