@use "base/variables" as v;
@use "base/mixins" as m;

.acciones {
  margin-top: 5rem;

  @include m.tablet {
    display: flex;
    justify-content: space-between;
  }

  .boton {
    &:hover{
      color: v.$blanco;
    }
  }

  a {
    color: v.$blanco;
    display: block; //Salto de linea por elemento
    margin-bottom: calc(v.$separacion / 2);
    text-align: center;
    
    &:hover{
      color: darken(v.$gris , 30) ;
    }
  }

}

.accion-unica{

  @include m.tablet {
    justify-content: center;
  }

  a{
    font-size: 2rem;
  }
}
