


@keyframes fadein {
    0% {
    opacity: 0;
    transform: scale(1, 0.1);
    }
    100% {
    opacity: 1;
    transform: scale(1, 1);
    }
}
@keyframes fadeout {
    0% {
    opacity: 1;
    transform: scale(1, 1);
    }
    100% {
    opacity: 0;
    transform: scale(1, 0.1);
    }
}

@keyframes fadeinTopBottom {
    from {
        opacity:0;
        padding-top: 0;
    }
    to {
        opacity:1;
        padding-top: 1;
    }
}

@keyframes escala {
    0% {
    }
    100% {
        height: 1rem;
    }
}



