@use 'base/variables' as v;
@use 'base/mixins' as m;
.app{
    .tabs{

        @include m.tablet{
            display: flex;
            
        }
        
        button{
            
            @include m.boton(v.$azul, v.$blanco);
            padding: 1rem;
            display: block;
            margin: 0 0 1rem 0;
            text-transform: uppercase;
            border-radius: 1rem;

            @include m.tablet{
                width: 100%;
                &:nth-child(1){
                    border-radius: 1rem 0 0 1rem;
                }
                &:nth-child(2){
                    border-radius: 0;
                }
                &:nth-child(3){
                    border-radius: 0 1rem 1rem 0;
                }
            }

            :hover{
                cursor: pointer;
                background-color: darken(v.$azul, 1);
            }

            &.actual{
                background-color: v.$blanco;
                color: v.$azul;
            }
        }
    

    }
}
